'use client';

import React, { useState, useEffect } from 'react';

import ChildA from './child-a';

export default function Parent() {
  const [pData, setPData] = useState('parent data');

  return (
    <>
      <h2>Parent (父母)</h2>
      <hr />
      <ChildA pData={pData}/>
    </>
  );
}
